<i18n>
{
  "en": {
    "TransactionDetails": "Transaction details",
    "Recharge": "Recharge",
    "Consumption": "Consumption",
    "Withdrawal": "Withdrawal",
    "Empty": "Empty",
    "PaymentSuccess": "Payment success",
    "PaymentFailure": "Payment failure",
    "Unpaid": "Unpaid",
    "unit": "￥",
    "Balance": "Balance",
    "WeChat": "WeChat",
    "Alipay": "Alipay",
    "balancePayment": "Balance payment",
    "Charger": "Charger",
    "Bettery": "Bettery",
    "BalanceText": "Balance",
    "Deposit": "Deposit",
    "portableBatteryCharging": "portable battery charging",
    "seatCharging": "seat charging",
    "weChatAccountNumber": "weChat account",
    "alipayAccount": "alipay account"
  },
  "zh": {
    "TransactionDetails": "交易明细",
    "Recharge": "充值",
    "Consumption": "消费",
    "Withdrawal": "提现",
    "Empty": "空空如也",
    "PaymentSuccess": "付款成功",
    "PaymentFailure": "付款失败",
    "Unpaid": "未付款",
    "unit": "元",
    "Balance": "余额消费",
    "WeChat": "微信支付",
    "Alipay": "支付宝支付",
    "balancePayment": "余额支付",
    "Charger": "座充",
    "Bettery": "充电宝",
    "BalanceText": "余额",
    "Deposit": "押金",
    "portableBatteryCharging": "充电宝充电",
    "seatCharging": "座充充电",
    "weChatAccountNumber": "微信账号",
    "alipayAccount": "支付宝账号"
  }
}
</i18n>

<template>
  <div class="transaction">
    <head-top :title='$t("TransactionDetails")' :go-back="true"></head-top>
    <div class="container">
      <div class="tabs">
        <div :class="['tab',{'active': currentTab === tab.type}]" v-for="(tab, index) in tabs" :key="index" @click="toggleTab(tab.type)">
          <span>{{tab.title}}</span>
        </div>
      </div>
      <div class="content">
        <div class="list" v-if="list[currentTab].data&&list[currentTab].data.length">
          <scroll ref="scroll"
                  :data="list[currentTab].data"
                  :pullDownRefresh="true"
                  :pullUpLoad="true"
                  @pullingDown="onPullingDown"
                  @pullingUp="onPullingUp">
            <div class="warp">
              <div class="item" v-for="(item, i) in list[currentTab].data" :key="i">
                <div class="left">
                  <p>
                    <span class="text" v-if="currentTab === 'recharge'">
                      {{item.recharge_type === 1 ? $t('BalanceText') : $t('Deposit')}}
                    </span>
                    <span class="text" v-if="currentTab === 'consumption'">
                      {{item.charging_type === 1 ? $t('portableBatteryCharging') : $t('seatCharging')}}
                    </span>
                    <span class="text" v-if="currentTab === 'withdrawal'">{{$t('BalanceText')}}</span>
                    <span class="state" v-if="item.result === 'SUCCESS'">{{ $t('PaymentSuccess')}}</span>
                    <span class="state" v-if="item.result === 'SHIPPED'">{{ $t('Unpaid')}}</span>
                    <span class="state" v-if="item.result === 'FAIL'">{{ $t('PaymentFailure')}}</span>
                  </p>
                  <span class="time">{{item.time_end}}</span>
                </div>
                <div class="right">
                  <span class="count"><strong>{{item.fee / 100}}</strong>{{ $t('unit') }}</span>
                  <span class="type" v-if="currentTab === 'recharge'">{{ item.pay_type===1?$t('WeChat'):$t('Alipay') }}</span>
                  <span class="type" v-if="currentTab === 'consumption'">
                    <em v-if="item.pay_type===1">{{$t('WeChat')}}</em>
                    <em v-if="item.pay_type===2">{{$t('Alipay')}}</em>
                    <em v-if="item.pay_type===3">{{$t('balancePayment')}}</em>
                  </span>
                  <span class="type" v-if="currentTab === 'withdrawal'">{{ item.pay_type===1?$t('weChatAccountNumber'):$t('alipayAccount') }}</span>
                </div>
              </div>
            </div>
          </scroll>
        </div>
        <div class="empty" v-else>
          <img src="../../assets/wallet_images/nothing.png" alt="">
          <span>{{ $t('Empty') }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import headTop from '../../components/Header/index'
export default {
  name: 'transaction',
  data () {
    return {
      tabs: [
        {
          title: this.$t('Recharge'),
          type: 'recharge'
        },
        {
          title: this.$t('Consumption'),
          type: 'consumption'
        },
        {
          title: this.$t('Withdrawal'),
          type: 'withdrawal'
        }
      ],
      currentTab: 'recharge',
      list: {
        recharge: [
          // {
          //   'result': 'SUCCESS',
          //   'recharge_type': 1,
          //   'fee': 1,
          //   'pay_type': 2,
          //   'time_end': '2018.12.18 15:32:00',
          //   'total_page': 3
          // }
        ],
        consumption: [
          // {
          //   'charging_type': 1,
          //   'result': 'SUCCESS',
          //   'fee': 1,
          //   'pay_type': 2,
          //   'time_end': '2018.12.18 15:32:00',
          //   'total_page': 3
          // }
        ],
        withdrawal: [
          // {
          //   'charging_type': 1,
          //   'result': 'SUCCESS',
          //   'fee': 1,
          //   'pay_type': 2,
          //   'time_end': '2018.12.18 15:32:00',
          //   'total_page': 3
          // }
        ]
      },
      page: 1 // 页码
    }
  },

  components: {
    headTop
  },

  mounted () {
    this.dataInteractionResponse(res => {
      console.log(res)
      switch (res.msgType) {
        // 查询用户消费明细返回
        case '0x5262':
          if (res.result && res.data) {
            if (this.page === 1) {
              this.list[this.currentTab] = res.data
            } else {
              res.data.data.map(i => {
                this.list[this.currentTab].data.push(i)
              })
            }
          }
          break
      }
    })
    // 查询户消费明细请求
    this.dataInteractionRequest({
      msgType: '0x5261',
      transactionType: 1,
      page: this.page,
      count: 10
    })
    if (this.ISDEBUG) {
      let str = '{"msgType":"0x5262","result":true,"data":{"transactionType":1,"page":1,"count":10,"total_page":1,"data":[{"fee":1,"recharge_type":1,"pay_type":2,"result":"SHIPPED","time_end": "2018.12.18 15:32:00"}]}}'
      window.dataInteractionResponse(str)
    }
  },
  methods: {
    analogData () {
      let amount = parseInt(Math.random() * 1000)
      let str
      switch (this.currentTab) {
        case 'recharge':
          str = `{"msgType":"0x5262","result":true,"data":{"transactionType":1,"page":1,"count":10,"total_page":10,"data":[{"fee":${amount},"recharge_type":1,"pay_type":2,"result":"SHIPPED","time_end": "2018.12.18 15:32:00"}]}}`
          window.dataInteractionResponse(str)
          break
        case 'consumption':
          str = `{"msgType":"0x5262","result":true,"data":{"transactionType":1,"page":1,"count":10,"total_page":10,"data":[{"fee":${amount},"charging_type":1,"pay_type":2,"result":"SHIPPED","time_end": "2018.12.18 15:32:00"}]}}`
          window.dataInteractionResponse(str)
          break
        case 'withdrawal':
          break
      }
    },

    toggleTab (type) {
      // 回到顶部
      this.$refs.scroll && this.$refs.scroll.scrollTo(0, 0)
      this.currentTab = type
      this.page = 1
      this.updatePage(this.currentTab, this.page)
      if (this.ISDEBUG) {
        this.analogData()
      }
    },

    onPullingDown () {
      console.log(111)
      // setTimeout(() => {
      //   this.$refs.scroll.forceUpdate()
      // }, 2000)
      this.page = 1
      this.updatePage(this.currentTab, this.page)
      if (this.ISDEBUG) {
        this.analogData()
      }
    },

    onPullingUp () {
      if (this.list[this.currentTab].total_page > this.page) {
        this.updatePage(this.currentTab, ++this.page)
        if (this.ISDEBUG) {
          this.analogData()
        }
      } else {
        this.$refs.scroll.forceUpdate()
      }
    },
    // 更新明细数据
    updatePage (type, page) {
      let requestType
      switch (type) {
        case 'recharge':
          requestType = 1
          break
        case 'consumption':
          requestType = 2
          break
        case 'withdrawal':
          requestType = 3
          break
      }
      // 查询订单数据请求
      this.dataInteractionRequest({
        msgType: '0x5261',
        transactionType: requestType,
        page: page,
        count: 10
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.transaction{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    .tabs{
      display: flex;
      .tab{
        height: 80px;
        flex: 1;
        font-size: 28px;
        &.active{
          color: #f4da21;
          span::after{
            content: '';
            display: block;
            .wh(50px,6px);
            background: url('../../assets/wallet_images/sliding.png');
            margin: -10px auto 0 auto;
          }
        }
        span{
          display: block;
          line-height: 80px;
        }
      }
    }
    .content{
      .wh(100%, calc(~"100% - 80px"));
      background: #eeeeee;
      overflow-y: scroll;
      .empty{
        img{
          height: 190px;
          margin: 260px auto 0 auto;
        }
        span{
          display: block;
          font-size: 30px;
        }
        a{
          font-style: italic;
          font-size: 30px;
          color: #50a4ff;
          text-decoration: underline;
          display: block;
          margin-top: 90px;
          &:active{
            background: none;
          }
        }
      }
      .list{
        .wh(100%, 100%);
        padding-top: 30px;
        background: #ffffff;
        .warp{
          .wh(100%, 100%);
          .item{
            &:nth-child(1){
              margin: 0 auto;
            }
            &:last-child{
              margin-bottom: 20px;
            }
            margin: 44px auto 0 auto;
            .wh(680px, 150px);
            background: #ffffff;
            border-radius: 20px;
            box-shadow: 0px 4px 25px rgba(130,130,130,0.2); // 左上下右
            display: flex;
            .left{
              .wh(490px, 100%);
              p{
                font-size: 28px;
                height: 75px;
                color: #595959;
                line-height: 75px;
                text-align: left;
                .text{
                  margin: 0 30px 0 30px;
                }
              }
              .time{
                display: block;
                .wh-l(100%, 75px);
                text-align: left;
                font-size: 22px;
                color: #999999;
                padding-left: 30px;
              }
            }
            .right{
              width: 190px;
              padding: 30px 30px 0 0;
              position: relative;
              span{
                display: block;
                font-size: 20px;
                color: #999999;
                text-align: right;
                &.count{
                  font-size: 30px;
                  color: #F4233D;
                  strong{
                    font-size: 46px;
                    margin-right: 10px;
                    font-weight: bold;
                  }
                }
              }
              .type{
                .wh(140px, 30px);
                .zoom-font(0.7);
                float: right;
                margin-right: -42px;
              }
            }
          }
        }
      }
    }
  }
}
</style>



